body{
  margin: 0;
  background: url('https://wallpapers-fenix.eu/full/141213/002818494.jpg');
  background-size: cover;
  color: rgb(230, 230, 230);;
}
.title{
  margin: 20px 0 0 0;
}
.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 15px;
  margin: 0 auto;
  padding: 0 20px;
  width: 1200px;
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 0 150px rgb(0, 0, 0);
}
.description{
  list-style: none;
  text-align: center;
}
.description__item{
  margin-top: 15px;
  font-size: 24px;
}
.keyboard {
  display: flex;
  flex-wrap: wrap;
  gap:10px;
  width: 1100px;
  height: 400px;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.3);
  border: 2px solid rgba(0, 0, 0, 0.308); 
  box-shadow: 0 0 50px rgb(225, 105, 255);
  border-radius: 8px;
}
.textarea{
  width: 70%;
  min-height: 300px;
  background-color: transparent;
  font-size: 20px;
  color: rgb(230, 230, 230);
  border: 2px rgb(230, 230, 230) solid;
  border-radius: 8px;
}
.key{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 6%;
  height: 15%;
  color:antiquewhite;
  font-size: 22px;
  font-weight: 700;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  box-shadow: 0 0 10px black;
  transition: all 0.3s;
}

.key--big{
  width: 10%;
}
.key--extra-big{
  width: 37.7%;
}

.key--active{
  background-color: black;
  transform: scale(0.85 );
}

.capslock::after{
  position: absolute;
  content: '';
  right: 7px;
  top: 7px;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background-color: rgba(255,255,255, 0.3);
}
.capslock--active::after{
  background-color: rgb(212, 40, 40);
}